import { addDoc, collection, deleteDoc, doc, getDoc, getDocs, updateDoc } from "firebase/firestore";



class PlayerService {
    static criar = (db, callback, player) => {
        addDoc(collection(db, 'players'), player)
        .then(
            (docRef)=> {
                callback(docRef.id)
            }
        )
        .catch(error=>console.log(error))
    }

    static getPlayers = (db,callback) => {
        getDocs(collection(db,'players'))
        .then (
            (snapshot)=> {
                const players = []
                snapshot.forEach(
                    (document)=>{
                        const id = document.id
                        const {nomePlayer, nomePersonagem, elemento, vidaAtual, vidaTotal, sanidadeAtual, sanidadeTotal, peAtual, peTotal, itens, pesoTotal} = document.data()
                        players.push({id,nomePlayer, nomePersonagem, elemento, vidaAtual, vidaTotal, sanidadeAtual, sanidadeTotal, peAtual, peTotal, itens, pesoTotal})
                    }
                )
                callback(players)
            }
        )
        .catch(error=>console.log(error))
    }

    static getItens = (db, id, callback) => {
        getDocs(collection(db,"players/"+ id +"/itens"))
        .then (
            (snapshot)=> {
                const itens = []
                snapshot.forEach(
                    (document)=>{
                        const id = document.id
                        const {nome, categoria, espaco} = document.data()
                        itens.push({id, nome, categoria, espaco})
                    }
                )
                callback(itens)
            }
        )
        .catch(error=>console.log(error))
    }

    static getPericias = (db, id, callback) => {
        getDocs(collection(db,"players/"+ id +"/pericias"))
        .then (
            (snapshot)=> {
                const pericias = []
                snapshot.forEach(
                    (document)=>{
                        const id = document.id
                        const {nome, dados, treino, bonus, outros, espaco} = document.data()
                        pericias.push({id, nome, dados, treino, bonus, outros, espaco})
                    }
                )
                callback(pericias)
            }
        )
        .catch(error=>console.log(error))
    }

    static getAtaques = (db, id, callback) => {
        getDocs(collection(db,"players/"+ id +"/ataques"))
        .then (
            (snapshot)=> {
                const ataques = []
                snapshot.forEach(
                    (document)=>{
                        const id = document.id
                        const {tipo, nome, categoria, teste, dano, critico, alcance, bonus, modificadores} = document.data()
                        ataques.push({id, tipo, nome, categoria, teste, dano, critico, alcance, bonus, modificadores})
                    }
                )
                callback(ataques)
            }
        )
        .catch(error=>console.log(error))
    }

    static getHabilidades = (db, id, callback) => {
        getDocs(collection(db,"players/"+ id +"/habilidades"))
        .then (
            (snapshot)=> {
                const ataques = []
                snapshot.forEach(
                    (document)=>{
                        const id = document.id
                        const {tipo, nome, custo, pagina, descritivo} = document.data()
                        ataques.push({id, tipo, nome, custo, pagina, descritivo})
                    }
                )
                callback(ataques)
            }
        )
        .catch(error=>console.log(error))
    }

    static adicionarItem = (db, id, callback, item) => {
        addDoc(collection(db,"players/"+ id +"/itens"), item)
        .then(
            (docRef)=> {
                callback(docRef.id)
            }
        )
        .catch(error=>console.log(error))
    }

    static adicionarPericia = (db, id, callback, pericia) => {
        addDoc(collection(db,"players/"+ id +"/pericias"), pericia)
        .then(
            (docRef)=> {
                callback(docRef.id)
            }
        )
        .catch(error=>console.log(error))
    }

    static adicionarAtaque = (db, id, callback, ataque) => {
        addDoc(collection(db,"players/"+ id +"/ataques"), ataque)
        .then(
            (docRef)=> {
                callback(docRef.id)
            }
        )
        .catch(error=>console.log(error))
    }

    static adicionarHabilidade = (db, id, callback, habilidade) => {
        addDoc(collection(db,"players/"+ id +"/habilidades"), habilidade)
        .then(
            (docRef)=> {
                callback(docRef.id)
            }
        )
        .catch(error=>console.log(error))
    }

    static getPlayerById = (db, callback, id) => {
        getDoc(doc(db, 'players', id))
        .then(
            (docSnap)=>{
                if(docSnap.exists()){
                    callback(docSnap.data())
                }
            }
        )
        .catch(error=>console.log(error))
    }

    static atualizar = (db, callback, id, player)=>{
        updateDoc(doc(db,'players',id), player)
        .then(
            ()=>{
                callback()
            }
        )
        .catch(error=>console.log(error))
    }

    static atualizarItem = (db, callback, idPlayer, idItem, item)=>{
        updateDoc(doc(db, ("players/"+ idPlayer +"/itens"), idItem), item)
        .then(
            ()=>{
                callback()
            }
        )
        .catch(error=>console.log(error))
    }

    static atualizarPericia = (db, callback, idPlayer, idPericia, pericia)=>{
        updateDoc(doc(db, ("players/"+ idPlayer +"/pericias"), idPericia), pericia)
        .then(
            ()=>{
                callback()
            }
        )
        .catch(error=>console.log(error))
    }

    static atualizarAtaque = (db, callback, idPlayer, idAtaque, ataque)=>{
        updateDoc(doc(db, ("players/"+ idPlayer +"/ataques"), idAtaque), ataque)
        .then(
            ()=>{
                callback()
            }
        )
        .catch(error=>console.log(error))
    }

    static atualizarHabilidade = (db, callback, idPlayer, idHabilidade, habilidade)=>{
        updateDoc(doc(db, ("players/"+ idPlayer +"/habilidades"), idHabilidade), habilidade)
        .then(
            ()=>{
                callback()
            }
        )
        .catch(error=>console.log(error))
    }

    static apagar = (db, callback, id) => {
        deleteDoc(doc(db,'players', id))
        .then(()=> {
            callback(true)
        })
        .catch(error=>console.log(error))
    }

    static apagarItem = (db, idPlayer, callback, idItem) => {
        deleteDoc(doc(db,('players/' + idPlayer + '/itens'), idItem))
        .then(()=> {
            callback(true)
        })
        .catch(error=>console.log(error))
    }

    static apagarPericia = (db, idPlayer, callback, idPericia) => {
        deleteDoc(doc(db,('players/' + idPlayer + '/pericias'), idPericia))
        .then(()=> {
            callback(true)
        })
        .catch(error=>console.log(error))
    }

    static apagarAtaque = (db, idPlayer, callback, idAtaque) => {
        deleteDoc(doc(db,('players/' + idPlayer + '/ataques'), idAtaque))
        .then(()=> {
            callback(true)
        })
        .catch(error=>console.log(error))
    }

    static apagarHabilidade = (db, idPlayer, callback, idHabilidade) => {
        deleteDoc(doc(db,('players/' + idPlayer + '/habilidades'), idHabilidade))
        .then(()=> {
            callback(true)
        })
        .catch(error=>console.log(error))
    }
}

export default PlayerService;