import "./CriarPersonagem.css"
import { useState } from "react";
import { useNavigate } from "react-router-dom";

import PlayerService from "../../service/PlayerService";
import db from "../../../FirebaseConfig";


const CriarPersonagem = () => {
    const navigate = useNavigate();

    const [nomePlayer, setNomePlayer] = useState('');
    const [nomePersonagem, setNomePersonagem] = useState('');

    const [imageToken, setImageToken] = useState("https://i.imgur.com/sSPd7XH.png");

    const [afinidade, setAfinidade] = useState('');

    const [nex, setNEX] = useState('');

    const [agilidade, setAgilidade] = useState(0);
    const [intelecto, setIntelecto] = useState(0);
    const [vigor, setVigor] = useState(0);
    const [presenca, setPresenca] = useState(0)
    const [forca, setForca] = useState(0);

    const [vidaAtual, setVidaAtual] = useState(0);
    const [vidaTotal, setVidaTotal] = useState(0);

    const [sanidadeAtual, setSanidadeAtual] = useState(0);
    const [sanidadeTotal, setSanidadeTotal] = useState(0);

    const [peAtual, setPeAtual] = useState(0);
    const [peTotal, setPeTotal] = useState(0);

    const [peRodada, setPeRodada] = useState(1);
    const [deslocamento, setDeslocamento] = useState(0);

    const [origem, setOrigem] = useState('');
    const [classe, setClasse] = useState('');

    const [idade, setIdade] = useState(0);
    const [localResidencia, setLocalResidencia] = useState('');

    const [pontosDePrestigio, setPontosDePrestigio] = useState(0);
    const [pesoTotal, setPesoTotal] = useState(0);

    const [abaInfoImpActive, setAbaInfoImpActive] = useState("active");
    const [abaOutrosActive, setAbaOutrosActive] = useState("");

    const acaoSubmite = () => {
        PlayerService.criar(
            db,
            (id)=> {
                navigate("/")
            },
            {
                imageToken,
                nomePlayer, 
                nomePersonagem, 
                vidaAtual, 
                vidaTotal, 
                sanidadeAtual, 
                sanidadeTotal, 
                peAtual, 
                peTotal, 
                afinidade,
                nex,
                agilidade, 
                intelecto, 
                vigor, 
                presenca, 
                forca,
                peRodada,
                deslocamento,
                idade,
                origem,
                classe,
                localResidencia,
                pesoTotal,
            }
        )
    }

    const setInput = (setFunction, valor) => {
        setFunction(valor);    
    }

    const Abas = () => {

        const ButtonAba = ({id, active, text}) => {
            return (
                <button 
                    id={id}
                    className={"opcao " + active}
                    onClick={()=>changeAba(id)}>
                        {text}
                </button>

            )
        }
    
        const changeAba = (stringAba) => {            
            if (stringAba === "abaInfoImportantes") {
                setAbaInfoImpActive("active");
                setAbaOutrosActive("");

            } else if (stringAba === "abaOutros") {
                setAbaInfoImpActive("");
                setAbaOutrosActive("active");
            }
        }

        return (
            <div className="janelasDeOpcoes">
                <ButtonAba id="abaInfoImportantes" active={abaInfoImpActive} text="Info. importantes"/>
                <ButtonAba id="abaOutros" active={abaOutrosActive} text="Outros"/>
            </div>
        )
    }

    const ButtonOption = () => {
        return (
            <div className="buttonOption">
                <button className="button line" onClick={()=>navigate("/")}>CANCELAR</button>
                <button className="button full" onClick={()=>acaoSubmite()}>CRIAR</button>
            </div>
        )
    }

    return (
        <div className="criarPersonagem">
            <div className="titulo">
                <h1 className="texto">CRIAR PERSONAGEM</h1>
                <div className="divisao"></div>
            </div>
            <Abas/>
            <div className={"modeloGeral infoImportantes " + abaInfoImpActive}>
                <div className="column1">
                    <div className="input">
                        <h1 className="titulo">Jogador</h1>
                        <input type="text" className="conteudo" value={nomePlayer} onChange={(nomePlayer)=>setInput(setNomePlayer, nomePlayer.target.value)}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Personagem</h1>
                        <input type="text" id="inputPersonagem" className="conteudo" value={nomePersonagem} onChange={(nomePersonagem)=>setInput(setNomePersonagem, nomePersonagem.target.value)}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Idade</h1>
                        <input type="text" id="inputPersonagem" className="conteudo" value={idade} onChange={(idade)=>setInput(setIdade, parseInt(idade.target.value))}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Local de residência</h1>
                        <input type="text" id="inputPersonagem" className="conteudo" value={localResidencia} onChange={(localResidencia)=>setInput(setLocalResidencia, localResidencia.target.value)}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Deslocamento</h1>
                        <input type="text" id="inputDeslocamento" className="conteudo" value={deslocamento} onChange={(deslocamento)=>setInput(setDeslocamento, deslocamento.target.value)}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Origem</h1>
                        <select name="select" className="conteudo" value={origem} onChange={(origem)=>setOrigem(origem.target.value)}>
                            <option className="option" value="" style={{display:"none"}}></option>
                            <option className="option" value="Acadêmico">Acadêmico</option>
                            <option className="option" value="Agente de Saúde">Agente de Saúde</option>
                            <option className="option" value="Amnésico">Amnésico</option>
                            <option className="option" value="Artista">Artista</option>
                            <option className="option" value="Atleta">Atleta</option>
                            <option className="option" value="Chef">Chef</option>
                            <option className="option" value="Criminoso">Criminoso</option>
                            <option className="option" value="Cultista Arrependido">Cultista Arrependido</option>
                            <option className="option" value="Desgarrado">Desgarrado</option>
                            <option className="option" value="Engenheiro">Engenheiro</option>
                            <option className="option" value="Executivo">Executivo</option>
                            <option className="option" value="Investigador">Investigador</option>
                            <option className="option" value="Lutador">Lutador</option>
                            <option className="option" value="Magnata">Magnata</option>
                            <option className="option" value="Mercenário">Mercenário</option>
                            <option className="option" value="Militar">Militar</option>
                            <option className="option" value="Operário">Operário</option>
                            <option className="option" value="Policial">Policial</option>
                            <option className="option" value="Religioso">Religioso</option>
                            <option className="option" value="Servidor Público">Servidor Público</option>
                            <option className="option" value="Teórico da Conspiração">Teórico da Conspiração</option>
                            <option className="option" value="T.I.">T.I.</option>
                            <option className="option" value="Trabalhador Rural">Trabalhador Rural</option>
                            <option className="option" value="Trambiqueiro">Trambiqueiro</option>
                            <option className="option" value="Universitário">Universitário</option>
                            <option className="option" value="Vítima">Vítima</option>
                            
                        </select>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Classe</h1>
                        <select name="select" className="conteudo" value={classe} onChange={(classe)=>setClasse(classe.target.value)}>
                            <option className="option" value="" style={{display:"none"}}></option>
                            <option className="option" value="Ocultista">Ocultista</option>
                            <option className="option" value="Combatente">Combatente</option>
                            <option className="option" value="Especialista">Especialista</option>
                        </select>
                    </div>
                    <div className="input">
                        <h1 className="titulo">NEX</h1>
                        <select name="select" className="conteudo" value={nex} onChange={(nex)=>setNEX(nex.target.value)}>
                            <option className="option" value="" style={{display:"none"}}></option>
                            <option className="option" value="0%">0%</option>
                            <option className="option" value="5%">5%</option>
                            <option className="option" value="10%">10%</option>
                            <option className="option" value="15%">15%</option>
                            <option className="option" value="20%">20%</option>
                            <option className="option" value="25%">25%</option>
                            <option className="option" value="30%">30%</option>
                            <option className="option" value="35%">35%</option>
                            <option className="option" value="40%">40%</option>
                            <option className="option" value="45%">45%</option>
                            <option className="option" value="50%">50%</option>
                            <option className="option" value="55%">55%</option>
                            <option className="option" value="60%">60%</option>
                            <option className="option" value="65%">65%</option>
                            <option className="option" value="70%">70%</option>
                            <option className="option" value="75%">75%</option>
                            <option className="option" value="80%">80%</option>
                            <option className="option" value="85%">85%</option>
                            <option className="option" value="90%">90%</option>
                            <option className="option" value="95%">95%</option>
                            <option className="option" value="99%">99%</option>
                        </select>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Afinidade</h1>
                        <select name="select" id="selectAfinidade" className="conteudo" value={afinidade} onChange={(afinidade)=>setAfinidade(afinidade.target.value)}>
                         <option className="option" value="" style={{display:"none"}}></option>
                            <option className="option" value="nenhuma">Nenhuma</option>
                            <option className="option" value="morte">Morte</option>
                            <option className="option" value="sangue">Sangue</option>
                            <option className="option" value="energia">Energia</option>
                            <option className="option" value="conhecimento">Conhecimento</option>
                        </select>
                    </div>             
                </div>
                <div className="column2">
                    <div className="input">
                        <h1 className="titulo">Força</h1>
                        <input type="text" id="inputForca" className="conteudo" value={forca} onChange={(forca)=>setInput(setForca, parseInt(forca.target.value))}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Agilidade</h1>
                        <input type="text" id="inputAgilidade" className="conteudo" value={agilidade} onChange={(agilidade)=>setInput(setAgilidade, parseInt(agilidade.target.value))}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Intelecto</h1>
                        <input type="text" id="inputIntelecto" className="conteudo" value={intelecto} onChange={(intelecto)=>setInput(setIntelecto, parseInt(intelecto.target.value))}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Vigor</h1>
                        <input type="text" id="inputVigor" className="conteudo" value={vigor} onChange={(vigor)=>setInput(setVigor, parseInt(vigor.target.value))}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Presença</h1>
                        <input type="text" id="inputPresenca" className="conteudo" value={presenca} onChange={(presenca)=>setInput(setPresenca, parseInt(presenca.target.value))}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Pontos de vida</h1>
                        <input type="text" id="inputPVTotal" className="conteudo" value={vidaTotal} onChange={(vidaTotal)=>{setInput(setVidaTotal, parseInt(vidaTotal.target.value)); setInput(setVidaAtual, parseInt(vidaTotal.target.value))}}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Sanidade</h1>
                        <input type="text" id="inputSANTotal" className="conteudo" value={sanidadeTotal} onChange={(sanidadeTotal)=>{setInput(setSanidadeTotal, parseInt(sanidadeTotal.target.value)); setInput(setSanidadeAtual, parseInt(sanidadeTotal.target.value))}}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Pontos de esforço</h1>
                        <input type="text" id="inputPETotal" className="conteudo" value={peTotal} onChange={(peTotal)=>{setInput(setPeTotal, parseInt(peTotal.target.value)); setInput(setPeAtual, parseInt(peTotal.target.value))}}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Pontos de prestigio</h1>
                        <input type="text" id="inputPontosDePrestigio" className="conteudo" value={pontosDePrestigio} onChange={(pontosDePrestigio)=>{setInput(setPontosDePrestigio, parseInt(pontosDePrestigio.target.value))}}/>
                    </div>
                </div>
            </div>
            <div className={"modeloGeral outros " + abaOutrosActive}>
                <div className="column1">    
                    <div className="input">
                        <h1 className="titulo">PE por rodada</h1>
                        <input type="text" id="PERodada" className="conteudo" value={peRodada} onChange={(e)=>setInput(setPeRodada, parseInt(e.target.value))}/>
                    </div>
                    <div className="input">
                        <h1 className="titulo">Peso total</h1>
                        <input type="text" id="pesoTotal" className="conteudo" value={pesoTotal} onChange={(e)=>setInput(setPesoTotal, parseInt(e.target.value))}/>
                    </div>
                </div>
                <div className="column2">
                    <div className="input">
                        <h1 className="titulo">Imagem token</h1>
                        <input type="text" id="inputImageToken" className="conteudo" value={imageToken} onChange={(e)=>setInput(setImageToken, e.target.value)}/>
                    </div>
                </div>
            </div>
            <ButtonOption/>
        </div>
    )
}

export default CriarPersonagem;