import "./App.css";
import { useEffect, useState } from "react";
import { BrowserRouter, Route, Routes } from "react-router-dom";

import Player from "./components/pages/Player/Player";
import PlayersView from "./components/pages/Player/PlayersView";
import PlayerService from "./components/service/PlayerService";
import db from "./FirebaseConfig";
import CriarPersonagem from "./components/pages/CriarPersonagem/CriarPersonagem";
import Teste from "./components/pages/CriarPersonagem/CriarPersonagem";
import Backup from "./components/pages/Mestre/Backup";
import EditarPersonagem from "./components/pages/CriarPersonagem/EditarPersonagem";

export const App = () => {
  const [players, setPlayers] = useState([]);

  useEffect(
    () => {
        PlayerService.getPlayers(
            db, 
            (players) => {
                setPlayers(players)
            }
        )
    },
    ['']
    )
    
  return ( 
    <section className="app">
      <section className="content">
      <BrowserRouter>
        <Routes id="Routes">
          <Route
            path="/"
            element={<PlayersView/>}
          />
          <Route
            path="/player/:id"
            element={<Player/>}
          />
          <Route
            path="/adicionarplayer"
            element={<CriarPersonagem/>}
          />
          <Route
            path="/player/edit/:id"
            element={<EditarPersonagem/>}
          />
        </Routes>
      </BrowserRouter>
      </section>
    </section>
  )
}