import FlatList from "flatlist-react";
import { useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import db from "../../../FirebaseConfig";
import PlayerService from "../../service/PlayerService";
import x from "../../svgs/x.svg"

import lixeira from "../../svgs/trash.svg"


import "./PlayersView.css";

const PlayersView = () => {
    const navigate = useNavigate();

    const [players, setPlayers] = useState([]);
    const [playerExcluir, setPlayerExcluir] = useState({id: "xxx",nome: ""});

    useEffect(
        () => {
            PlayerService.getPlayers(
                db, 
                (players) => {
                    setPlayers(players)
                }
            )
        },
        []
    )

    const backup = () => {
        for (let player of players) {
            let dataPlayer = "text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(player));
            var a = document.createElement("a");
            document.body.appendChild(a);
            a.style = "display: none";
            a.href = 'data:' + dataPlayer;
            a.download = player.nomePlayer + " (atributos).json";
            a.click();

            PlayerService.getItens(
                db,
                player.id,
                (itens)=>{
                    const itensPlayer = {
                        nome: player.nomePlayer,
                        itens: itens
                    }
                    let dataItens = "text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(itensPlayer));
                    var a = document.createElement("a");
                    document.body.appendChild(a);
                    a.style = "display: none";
                    a.href = 'data:' + dataItens ;
                    a.download = player.nomePlayer + " (itens).json";
                    a.click();
                }
            );
            PlayerService.getPericias(
                db,
                player.id,
                (pericias)=>{
                    const periciasPlayer = {
                        nome: player.nome,
                        pericias: pericias
                    }
                    let dataPericias = "text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(periciasPlayer));
                    var a = document.createElement("a");
                    document.body.appendChild(a);
                    a.style = "display: none";
                    a.href = 'data:' + dataPericias ;
                    a.download = player.nomePlayer + " (pericias).json";
                    a.click();
                }
            );
            PlayerService.getAtaques(
                db,
                player.id,
                (ataques)=>{
                    const ataquesPlayer = {
                        nome: player.nome,
                        ataques: ataques
                    }
                    let dataAtaques = "text/json;charset=utf-8," + encodeURIComponent(JSON.stringify(ataquesPlayer));
                    var a = document.createElement("a");
                    document.body.appendChild(a);
                    a.style = "display: none";
                    a.href = 'data:' + dataAtaques ;
                    a.download = player.nomePlayer + " (ataques).json";
                    a.click();
                }
            );
        }
    }


    const apagarPlayer = (id) => {
        PlayerService.apagar(
            db,
            (resultado) => {
                let playerResultado = players.filter(
                    (player)=>player.id !== id
                )
                setPlayers(playerResultado)
            },
            id
        )
    }

    const ApagarPlayer = () => {
        return (
            <div className="modal modalApagarPlayer">
            <div className="contentModal">
            <div className="cabecalhoModal">
                        <h1 className="titulo">Excluir player</h1>
                        <button alt="" className="buttonFechar" onClick={()=>toggleApagarPlayer("fechar")}>
                            <img alt="" src={x}/>
                        </button>
                    </div>
                    <div className="content">
                        <h1 className="texto">Tem certeza que deseja excluir {playerExcluir.nomePersonagem}?</h1>
                        <div className="buttons">
                                <button className="button" onClick={()=>toggleApagarPlayer("fechar")}>CANCELAR</button>
                                <button className="button adicionar" onClick={()=>apagarPlayer(playerExcluir.id)}>EXCLUIR</button>
                            </div>
                    </div>
            </div>
        </div>
        )
    }

    const toggleApagarPlayer = (value) => {
        const modalApagarPlayer = document.querySelector(".modalApagarPlayer")
        
        if(value === "abrir"){
            modalApagarPlayer.style.display = "";
            modalApagarPlayer.style.display = "flex";
        } else if (value === "fechar"){
            modalApagarPlayer.style.display = "";
            modalApagarPlayer.style.display = "none";
        }
    }

    return (
        <section className="playersView">
            <ApagarPlayer/>
            <div className="visualizarPlayers">
                <h1 className="titulo">JOGADORES</h1>
                <FlatList 
                list={players} 
                renderItem={item => 
                    <div className="selectPlayer">
                        <div key={item.nomePlayer} className="player" onClick={()=>navigate("/player/"+item.id)}>
                            <div className="info">
                                <h1 className="nomeJogador">{item.nomePlayer}</h1>
                                <p className="nomePersonagem">{item.nomePersonagem}</p>
                            </div>
                        </div>
                        <button className="lixeira" onClick={()=> {
                            toggleApagarPlayer("abrir");
                            setPlayerExcluir(item);
                            }}>
                            <img alt='' src={lixeira}/>
                        </button>
                    </div>
                    }
                />
            </div>
            <div className="adicionarPlayer">
                    <button className="button" onClick={()=>navigate("/adicionarplayer")}>ADICIONAR</button>
                    <button className="button backup" onClick={()=>backup()}>BACKUP</button>
            </div>
        </section>
    )
}

export default PlayersView;